@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700&display=swap);

:root
{
    --primary: #3acbf7;
}

.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor:pointer;
}

.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background-color: #fff;
    color: #6568f4;
    transition: 250ms;
}

.btn--primary{
    background-color: #3acbf7;
    background-color: var(--primary);
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border-radius: 4px;
    border: 1px solid #3acbf7;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}   

.btn--medium{
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
    font-family: 'Truculenta', sans-serif;
    text-decoration: none;

}

.btn--large{
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
    font-family: 'Truculenta', sans-serif;
}
.NavbarItems
{
    background: linear-gradient(90deg, rgb(110,94,254) 0%, rgba(73,63,252,1)100%);
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.2rem;
    position: relative;    
    z-index: 100;
    
}

.navbar-logo{
    color:#fff;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    
    
}

.nav-menu
{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    -webkit-justify-content: end;
            justify-content: end;
    margin-right: 2rem;
    

}

.nav-links{
    font-family: 'Truculenta', sans-serif;
    color: #fff;
    text-decoration: none;
    padding: 0.5rem 1rem;
    font-size: larger;
}

.nav-links:hover
{
    background-color: #6d76f7;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.fa-bars{
    color: #fff;
}

.menu-icon{
    display: none;
}

.nav-links-mobile{
    display: none;
    font-size: 30px;
    font-family: 'Truculenta', sans-serif;
    
}

.moPic
{
    position: relative;
    z-index: 50;
    margin: 0;
    padding: 0;
    margin-top: 100px;


}

.stamp 
{
    -webkit-transform: rotate(12deg);
            transform: rotate(12deg);
      color: #555;
      font-size: 3rem;
      font-weight: 700;
      border: 0.25rem solid #555;
      display: inline-block;
      padding: 0.25rem 1rem;
      text-transform: uppercase;
      border-radius: 1rem;
      font-family: 'Courier';
      mix-blend-mode: multiply;
}

.name
{
    font-family: 'Caveat', cursive;
    color: white;
    position: relative;
    z-index: 1;
    font-size: 60px;
    -webkit-justify-content:start;
            justify-content:start;
    display: block;
    margin-left:auto;
    margin-right: auto;

}

@media screen and (max-width: 960px){
    .NavbarItems{
        position: relative;
    }

    .nav-menu
    {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    

    .nav-menu.active
    {
        background: #6668f4;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
        font-size: 1.5rem;
        font-family: 'Truculenta', sans-serif;
        
    }
    .nav-links
    {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        font-family: 'Truculenta', sans-serif;
    }

    .nav-linkz 
    {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        font-family: 'Truculenta', sans-serif;
        
    }

    .nav-links:hover
    {
        background-color: #7577fa;
        border-radius: 0;
    }
    .nav-linkz:hover
    {
        background-color: #7577fa;
        border-radius: 0;
    }

    .navbar-logo{
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 50%);
                transform: translate(25%, 50%);
        font-family: 'Caveat', cursive;
    }

    .menu-icon{
        display: block;
        position: absolute;
        top:0;
        right: 0;
        -webkit-transform: translate(-100%, 60%);
                transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times{
        color: #fff;
        font-size: 2rem;
    }

    .nav-links-mobile{
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: #4ad9e4;
        text-decoration: none;
        color: #fff;
        font-size: 1.5rem;
        
    }
    .nav-links-mobile:hover{
        background: #fff;
        color: #6568f4;
        transition: 250ms;

    }

    .input{
        display: none;
    }

    

    Button{display:none;}

    .footer{
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 2.5rem;
    }
}



