  .container {
    position: relative;
    height: 100%;
    top: 60px;
    left: calc(50% - 300px);
    display: -webkit-flex;
    display: flex;
    box-sizing: border-box;
    margin-top: 100px;
    float: left;
    /* width: 20%; */
    padding: 50px;
    margin: auto;
    margin-left: 100px;
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  
  .card {
    display: -webkit-flex;
    display: flex;
    height: 280px;
    width: 200px;
    border-radius: 10px;
    background-color: rgba(0,154,217,1);;
    color: rgba(0,154,217,1);
    box-shadow: -1rem 0 3rem #000;
    margin-left: -50px;
    transition: 0.4s ease-out;
    position: relative;
    left: 0px;
    padding: 5px;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .card .btn
  {
    -webkit-justify-content: center;
            justify-content: center;
  }

  .card:not(:first-child) {
      margin-left: -50px;
  }
  
  .card:hover {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    transition: 0.4s ease-out;
  }
  
  .card:hover ~ .card {
    position: relative;
    left: 50px;
    transition: 0.4s ease-out;
  }
  
  .title {
    color: black;
    font-weight: 300;
    position: absolute;
    left: 20px;
    top: 15px;
    text-align: center;
  }
  
  .bar {
    position: absolute;
    top: 100px;
    left: 20px;
    height: 5px;
    width: 150px;
  }
  
  .emptybar {
    background-color: #2e3033;
    width: 100%;
    height: 100%;
  }
  
  .filledbar {
    position: absolute;
    top: 0px;
    z-index: 3;
    width: 0px;
    height: 100%;
    background: rgb(0,154,217);
    background: linear-gradient(90deg, rgba(0,154,217,1) 0%, rgba(217,147,0,1) 65%, rgba(255,186,0,1) 100%);
    transition: 0.6s ease-out;
  }
  
  .card:hover .filledbar {
    width: 120px;
    transition: 0.4s ease-out;
  }
  
  .circle {
    position: absolute;
    top: 150px;
    left: calc(50% - 60px);
  }
  
  .stroke {
    stroke: black;
    stroke-dasharray: 360;
    stroke-dashoffset: 360;
    transition: 0.6s ease-out;
  }
  
  svg {
    fill: #17141d;
    stroke-width: 2px;
  }
  
  .card:hover .stroke {
    stroke-dashoffset: 100;
    transition: 0.6s ease-out;
  }


  .card img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
  }

